import Ball from "./ball";
import PGenerator from "./particles-generator";
import goToMainMenu from "./screens-changer";
import {playGameMusic, stopMusic} from './music-player';

let startTime;
let pingTimeout;
let pause;
let stop;
let left;
let right;
let soundState = true;
let soundTrackLink;

const canvas = document.getElementById('canvas');
const ctx = canvas.getContext('2d');
const ParticlesGenerator = new PGenerator(ctx);
canvas.width = 650;
canvas.height = 400;
let score = {
    score1: 0,
    score2: 0,
};
let keys = {}; let mouseCords = {y: 0}
let accelerometerGamma;

function Box(options) {
    this.x = options.x || 10;
    this.y = options.y || 10;
    this.width = options.width || 40;
    this.height = options.height || 50;
    this.color = options.color || '#FFFFFF';
    this.speed = options.speed || 2;
    this.gravity = options.gravity || 2;
}

var player1 = new Box({
    x: 10,
    y: 200,
    width: 15,
    height: 80,
    color: '#FFFFFF',
    gravity: 10
});
var player2 = new Box({
    x: 625,
    y: 100,
    width: 15,
    height: 80,
    color: '#FFFFFF',
    gravity: 10
});
var midLine = new Box({
    x: (canvas.width / 2) - 2.5,
    y: -1,
    width: 5,
    height: canvas.height + 1,
    color: '#FFFFFF'
});
let theBall = new Ball({
    x: (canvas.width / 2),
    y: (canvas.height / 2),
    oldY: 0,
    width: 15,
    height: 15,
    color: '#FFFFFF',
    speed: 1,
    gravity: 1
})
let particlesArray = new Array();

function input() {
    let player;
    if (left) {
        player = player1;
        botMovement(player2);
    } else if (right) {
        player = player2;
        botMovement(player1);
    }
    switch (localStorage.getItem('controls')) {
        case '0': {
            if (38 in keys) {
                if (player.y - player.gravity > 0) {
                    player.y -= player.gravity;
                }
            } else if (40 in keys) {
                if (player.y + player.height + player.gravity < canvas.height) {
                    player.y += player.gravity;
                }
            }
            break;
        }
        case '1': {
            const futurePlayerPos = (mouseCords.y - (player.height / 2));
            if (futurePlayerPos > 0) {
                if (futurePlayerPos + player.height < canvas.height) {
                    player.y = futurePlayerPos;
                }
            }
            break;
        }
        case '2': {
            const futurePlayerPos = ((canvas.height / 2) - (player.height / 2)) + accelerometerGamma * 5;
            if (futurePlayerPos > 0) {
                if (futurePlayerPos + player.height < canvas.height) {
                    player.y = futurePlayerPos;
                }
            }
            break;
        }
    }
}

function botMovement(player) {
    if ((theBall.oldY + 25 > theBall.y) || (theBall.oldY - 25 < theBall.y)) {
        theBall.oldY = theBall.y;
        let target;
        let futurePlayerPos;
        target = ((theBall.y + theBall.height / 2) + theBall.gravity * theBall.speed);
        if (player.y + player.height / 2 > target) {
            futurePlayerPos = player.y - player.gravity;
        } else if (player.y + player.height / 2 < target) {
            futurePlayerPos = player.y + player.gravity;
        }
        if (futurePlayerPos > 0) {
            if (futurePlayerPos + player.height < canvas.height) {
                player.y = futurePlayerPos;
            }
        }
    }
}

function drawBox(box) {
    ctx.fillStyle = box.color;
    ctx.fillRect(box.x, box.y, box.width, box.height);
}

function drawBall(ball) {
    ctx.fillStyle = ball.color;
    ball.rebuildBlocks();
    ctx.fillRect(ball.block1.x, ball.block1.y, ball.block1.width, ball.block1.height);
    ctx.fillRect(ball.block2.x, ball.block2.y, ball.block2.width, ball.block2.height);
    ctx.fillRect(ball.block3.x, ball.block3.y, ball.block3.width, ball.block3.height);
}

function drawParticles() {
    particlesArray.forEach(item => {
        item.draw();
    });
}

function displayScore1() {
    ctx.font = "20px retro";
    ctx.fillStyle = "rgb(255,255,255)";
    let str;
    // if (server_cords) { str = server_cords.score1 } else { str = score1 };
    str = score.score1;
    ctx.fillText(str, (canvas.width / 2) - 50, 30);
}

function displayScore2() {
    ctx.font = "20px retro";
    ctx.fillStyle = "rgb(255,255,255)";
    let str;
    // if (server_cords) { str = server_cords.score2 } else { str = score2 };
    str = score.score2;
    ctx.fillText(str, (canvas.width / 2) + 50, 30);
}

function displayInfo(info) {
    ctx.font = "24px retro";
    ctx.fillStyle = "rgb(255,255,255)";
    let str = info;
    ctx.fillText(str, (canvas.width / 2) - 175, (canvas.height / 2));
}

function displayNumbers(info) {
    ctx.font = "34px Arial";
    ctx.fillStyle = "rgb(255,255,255)";
    let str = info;
    ctx.fillText(str, (canvas.width / 2) - 10, (canvas.height / 2));
}

function playBeeps() {
    if (Math.round(Math.random() * 1) == 0) {
        document.getElementsByTagName('audio')[0].play().then(() => {
        });
    } else {
        document.getElementsByTagName('audio')[1].play().then(() => {
        });
    }
}

function playHit() {
    if (Math.round(Math.random() * 1) == 0) {
        document.getElementById('audio-hit1').play().then(() => {
        });
    } else {
        document.getElementById('audio-hit2').play().then(() => {
        });
    }
}

function ballBounce() {
    if (theBall.speed > 20) { theBall.speed = 20 } else if (theBall.speed < -20) { theBall.speed = -20 }
    if (((theBall.y + theBall.gravity + theBall.height) >= canvas.height)) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x, theBall.y, 10, 'up');
        theBall.gravity = theBall.gravity * -1;
        theBall.y += theBall.gravity;
        theBall.x += theBall.speed;
    } else if (((theBall.y + theBall.gravity) <= 0)) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x, theBall.y, 10, 'down');
        theBall.gravity = theBall.gravity * -1;
        theBall.y += theBall.gravity;
        theBall.x += theBall.speed;
    }
    ballCollision();
}


function ballCollision() {
    if (((theBall.x + theBall.speed <= player1.x + player1.width) && (theBall.x > player1.x + player1.width) && (theBall.y + theBall.height > player1.y) && (theBall.y < player1.y + player1.height))) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x + theBall.speed, theBall.y + theBall.height/2, 10, 'rht');
        theBall.speed = theBall.speed - 0.5;
        theBall.speed = theBall.speed * -1;
        if (theBall.gravity > 0 ) { theBall.gravity = (Math.random() * 3) + 0.5; } else { theBall.gravity = ((Math.random() * 3) + 0.5) * -1; }
    }
    else if (((theBall.y + theBall.height) > player1.y) && (theBall.y < player1.y + player1.height) && (((theBall.x + theBall.width/2) || (theBall.x)) < player1.x + player1.width) && (((theBall.x + theBall.width/2) || (theBall.x)) > player1.x)) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x, theBall.y, 10, 'up');
        theBall.gravity = theBall.gravity * -1;
        theBall.y += theBall.gravity;
        theBall.x += theBall.speed;
    }
    else if ((theBall.y < player1.y + player1.height) && (theBall.y > player1.y) && (((theBall.x + theBall.width/2) || (theBall.x)) < player1.x + player1.width) && (((theBall.x + theBall.width/2) || (theBall.x)) > player1.x)) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x, theBall.y, 10, 'down');
        theBall.gravity = theBall.gravity * -1;
        theBall.y += theBall.gravity;
        theBall.x += theBall.speed;
    }
    else if (((theBall.x + theBall.width + theBall.speed/2 >= player2.x) && (theBall.x < player2.x) && (theBall.y + theBall.height > player2.y) && (theBall.y < player2.y + player2.height))) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x + theBall.width, theBall.y + theBall.height/2, 10, 'lft');
        theBall.speed = theBall.speed + 0.5;
        theBall.speed = theBall.speed * -1;
        if (theBall.gravity > 0 ) { theBall.gravity = (Math.random() * 3) + 0.5; } else { theBall.gravity = ((Math.random() * 3) + 0.5) * -1; }
    }
    else if (((theBall.y + theBall.height) > player2.y) && (theBall.y < player2.y + player2.height) && (((theBall.x + theBall.width/2) || (theBall.x)) < player2.x + player2.width) && (((theBall.x + theBall.width/2) || (theBall.x)) > player2.x)) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x, theBall.y, 10, 'up');
        theBall.gravity = theBall.gravity * -1;
        theBall.y += theBall.gravity;
        theBall.x += theBall.speed;
    }
    else if ((theBall.y < player2.y + player2.height) && (theBall.y > player2.y) && (((theBall.x + theBall.width/2) || (theBall.x)) < player2.x + player2.width) && (((theBall.x + theBall.width/2) || (theBall.x)) > player2.x)) {
        playHit();
        particlesArray = ParticlesGenerator.generateParticles(theBall.x, theBall.y, 10, 'up');
        theBall.gravity = theBall.gravity * -1;
        theBall.y += theBall.gravity;
        theBall.x += theBall.speed;
    }
    else if (theBall.x + theBall.width < 0) {
        score.score2 ++;
        // this.theBall.speed = this.theBall.speed * -1;
        theBall.speed = 2;
        theBall.x = 50 + theBall.speed;
        theBall.y += theBall.gravity;
        playBeeps();
    } else if (theBall.x > canvas.width) {
        score.score1 ++;
        // this.theBall.speed = this.theBall.speed * -1;
        theBall.speed = -2;
        theBall.x = 550 + theBall.speed;
        theBall.y += theBall.gravity;
        playBeeps();
    } else {
        theBall.x += theBall.speed;
        theBall.y += theBall.gravity;
    }
    draw();
}

function draw() {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    displayScore1();
    displayScore2();
    drawBox(player1);
    drawBox(player2);
    drawBox(midLine);
    drawBall(theBall);
    drawParticles();
}

function scoreCheck() {
    if (score.score1 >= 10) {

    } else if (score.score2 >= 10) {

    }
}

function clearScore() {
    score = {
        score1: 0,
        score2: 0,
    }
}

function loop() {
    if (!pause && !stop) {
        ballBounce();
        scoreCheck();
        input();
        window.requestAnimationFrame(loop);
    } else {
        if (pause) {
            let message = document.createElement('span');
            message.innerText = `Game paused (Press 'P' to continue)`;
            document.getElementById('chat').appendChild(message); message.scrollIntoView();
        }
    }
}

document.getElementById('chatSendButton').onclick = sendMessage;

function sendMessage() {
    if (document.getElementById('chatInput').value) {
        if (localStorage.getItem('name')) {
            let message = document.createElement('span');
            message.innerText = `${localStorage.getItem('name')} says: ${document.getElementById('chatInput').value}`;
            document.getElementById('chat').appendChild(message); message.scrollIntoView();
        } else if (left) {
            let message = document.createElement('span');
            message.innerText = `player 1 says: ${document.getElementById('chatInput').value}`;
            document.getElementById('chat').appendChild(message); message.scrollIntoView();
        } else if (right) {
            let message = document.createElement('span');
            message.innerText = `player 2 says: ${document.getElementById('chatInput').value}`;
            document.getElementById('chat').appendChild(message); message.scrollIntoView();
        }
        document.getElementById('chatInput').value = '';
    }
}

function gameStartSingle() {
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            init(); document.getElementById('ping').innerText = '';
            createPlayers();
            createBall();
            displayInfo('start');
            stop = false;
            stopMusic();
            definePlayerSide();
            setTimeout(() => {
                let beep = document.getElementById('audio-beep3');
                beep.onended = () => { setTimeout(() => playGameMusic(), 250); }; beep.play();
            }, 250);
            loop();
}

function gameEndSingle() {
    stop = true;
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    stopMusic();
    clearChat();
    clearScore();
    goToMainMenu(true);
}

function clearChat() {
    document.getElementById('chat').innerHTML = '';
}

function createPlayers() {
    player1 = new Box({
        x: 10,
        y: 200,
        width: 15,
        height: 80,
        color: '#FFFFFF',
        gravity: 2
    });
    player2 = new Box({
        x: 625,
        y: 100,
        width: 15,
        height: 80,
        color: '#FFFFFF',
        gravity: 2
    });
}

function createBall() {
    theBall = new Ball({
        x: (canvas.width / 2),
        y: (canvas.height / 2),
        width: 15,
        height: 15,
        color: '#FFFFFF',
        speed: 2,
        gravity: 1
    })
}

function definePlayerSide() {
    ctx.font = "16px retro";
    ctx.fillStyle = "rgb(255,255,255)";
    if (Math.round(Math.random() * 1) == 0) {
        left = true;
    } else {
        right = true;
    }
    let sideStr;
    if (left) { sideStr = 'LEFT' } else if (right) { sideStr = 'RIGHT' }
    switch (localStorage.getItem('controls')) {
        case '0': {
            let message = document.createElement('span');
            message.innerText = `You are from the ${sideStr} side, use 'up' and 'down' on keyboard to control your paddle.`;
            document.getElementById('chat').appendChild(message); message.scrollIntoView();
            break;
        }
        case '1': {
            let message = document.createElement('span');
            message.innerText = `You are from the ${sideStr} side, use your mouse to control your paddle.`;
            document.getElementById('chat').appendChild(message); message.scrollIntoView();
            break;
        }
        case '2': {
            let message = document.createElement('span');
            message.innerText = `You are from the ${sideStr} side, use your device rotation to control your paddle.`;
            document.getElementById('chat').appendChild(message); message.scrollIntoView();
            break;
        }
    }
}

function init() {
    window.addEventListener('keydown', function (e) {
        keys[e.keyCode] = true;
        // e.preventDefault();
    });
    window.addEventListener('deviceorientation', ev => {
        if (ev.gamma) {
            accelerometerGamma = ev.gamma;
        }
    }, true);
    window.addEventListener('keyup', function (e) {
        delete keys[e.keyCode];
    });
    window.addEventListener('keydown', (e) => {
        if (!stop) {
            switch (e.keyCode) {
                case 80: {
                    if (!stop && document.getElementById('chatInput') != document.activeElement) {
                        pause = !pause;
                        if (!pause) { loop(); }
                    }
                    break;
                }
                case 13: {
                    document.getElementById('chatSendButton').click();
                }
            }
        }
    })
    document.getElementById('chatSendButton').onclick = sendMessage;
    canvas.onmousemove = (e) => {
        mouseCords.y = e.clientY;
    }
}

export {gameStartSingle, gameEndSingle}
